import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import reportWebVitals from './reportWebVitals';
import App from './App';
import store from './redux/state';

let rerenderDOM = () => {

  ReactDOM.render(
    <React.StrictMode>
        <App  
        state={store.getState()} 
        newComment={store.newComment.bind(store)} 
        changeColorThem={store.changeColorThem.bind(store)} 
        changeBurgerMenu={store.changeBurgerMenu.bind(store)} 
        renderBurgerBtn={store.renderBurgerBtn.bind(store)}
        />
    </React.StrictMode>,

    document.getElementById('root')
  );
};
rerenderDOM(store.getState());

store.subscribe(rerenderDOM);

reportWebVitals();
